/* forms */
.form {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; }

.form-container {
  display: flex;
  flex-direction: column;
  width: 32rem;
  padding: 2rem;
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
  list-style-type: none; }

.form-container li {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  margin-top: 1rem; }

input {
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem; }

/* Products */
.product-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.content-margined {
  margin: 2rem;
  font-size: 72.5%; }

.product-list {
  margin-bottom: 7rem; }

.table td,
.table th {
  vertical-align: middle !important; }
